import React from "react";

const Video = (props) =>{
    return(
        <div className={'video'}>
            <iframe className={'videoSize videoSizeMed'}  src={`https://www.youtube.com/embed/${props.videos}`} frameBorder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen/>
        </div>
    );
};

export default Video;