import React, {Component} from 'react';
import LoaderMini from "./LoaderMini/LoaderMini";

const Search = (props)=>{

    return (
      <div className={'search  marginAuto'}>
        <form onSubmit={props.fetchSearchData} className="form-inline searchSmall   marginAuto">
            <input className="form-control mr-sm-2" name={'input'} type="search" placeholder="Search" aria-label="Search"/>
            <button className="btn btn-outline-danger my-2 my-sm-0 searchButton" type="submit">
              <i className="fas fa-search"/>
            </button>
            {props.isLoadedSearch?<LoaderMini/>:''}
        </form>
      </div>
    );

}

export default Search;