import React from "react";
import {Link} from "react-router-dom";

import MoviesRec from "./recommendMovie";
import MovieP from "./componentMoviePage";
import Video from "./videoComponent";
import {ThemeContext} from "./darkMode";

const apiKey = '34c6dd52b40998a7fba114d180097301';


class MoviePage extends React.Component{
    state= {
        activeMovie: [],
        recommend:[],
        videos:[],
        movie_id:null
    };

    static contextType = ThemeContext;

    fetchOneMovie=async()=>{
        const movie_id = this.props.location.state.movie_id;
        const reqMovie = await fetch(`https://api.themoviedb.org/3/movie/${movie_id}?api_key=${apiKey}&language=en-US`);
        const resMovie = await reqMovie.json();
        this.setState({activeMovie:resMovie});

    };

    fetchSimiliar=async()=>{
        const movie_id = this.props.location.state.movie_id;
        const reqRecommends = await fetch(`https://api.themoviedb.org/3/movie/${movie_id}/similar?api_key=${apiKey}&language=en-US&page=1`);
        const resRecommends = await reqRecommends.json();
        this.setState({recommend:resRecommends.results, movie_id:resRecommends.results.id});

    };

    fetchVideos=async()=>{
        const movie_id = this.props.location.state.movie_id;
        const reqVideos = await fetch(`https://api.themoviedb.org/3/movie/${movie_id}/videos?api_key=${apiKey}&language=en-US`);
        const resVideos = await reqVideos.json();
        this.setState({videos:resVideos.results[0].key});
    };

    componentDidMount= () =>{

        this.fetchOneMovie();
        this.fetchSimiliar();
        this.fetchVideos();


    };
    addHistory = (results) => {

        const updateMoviesHistory = [JSON.parse(localStorage.getItem('history'))].flat(10);
        console.log(updateMoviesHistory);
        if(updateMoviesHistory[0]===null){
            updateMoviesHistory.splice(0,1);
        }

        updateMoviesHistory.push(results);
        localStorage.setItem('history',JSON.stringify(updateMoviesHistory));

    };
    componentDidUpdate= (prevState) =>{
        if(this.state.movie_id!==prevState.movie_id){
            this.componentDidMount();
        }
    };
    movieId=()=>{
      this.setState({movie_id:this.props.location.state.movie_id})
    };
    render(){
      const {darkMode,changeTheme} = this.context;
        return(
          <div className={`body width100Small  ${darkMode?'darkBody':''} `}>
            <div className={`container width100Small marginAuto ${darkMode?'darkBodySite':''}`}>
                <nav className={`navbar sticky-top navbar-light bg-light header red margin ${darkMode?'darkHeader':''}`}>
                    <Link to="/"><button className={darkMode?'btn btn-secondary':'btn btn-warning'} >
                        <i className="fas fa-arrow-left"/>
                    </button></Link>
                  <button className={darkMode?'btn btn-secondary':'btn btn-warning'} onClick={()=>{changeTheme()}}>
                    {darkMode? <i className="fas fa-sun"/>: <i className="fas fa-moon"/>}
                  </button>
                </nav>
                <MovieP activeMovie={this.state.activeMovie}/>
                <Video videos={this.state.videos}/>
                <div className={'marginAuto'}>
                    <h2>Recommendations:</h2>
                    <MoviesRec movieId={this.movieId} addHistory={this.addHistory} recommend={this.state.recommend}  />
                </div>

            </div>
          </div>
        );

}};

export default MoviePage;