import React from "react";
import {Link} from "react-router-dom";
import PopMovie from "./pop";
import {ThemeContext} from "./darkMode";


class Popup extends React.ComponentÂ {
  static contextType = ThemeContext;

  render() {
    const {darkMode}=this.context;
    const {closePopup, moviesWillWatch}=this.props;
    console.log("fff",moviesWillWatch);

    return (
      <div className='popup'>
        <div className={`popup_inner  width-80 popSmall d-flex f-wrap ${darkMode?'darkPopUp':''}`}>

          <button onClick={closePopup} className={` buttonPop ${darkMode?'btn btn-secondary popButton':'popButton btn btn-warning '}`}>
            <i className="fas fa-times"/>
          </button>
            {moviesWillWatch.map((movie)=>(


                  <div key={movie.id} className={`movieTablePop movieTablePopSmall ${darkMode?'darkTableMovie':''}`}  >
                    <Link to={{
                      pathname:`/MoviePage/${movie.id}`,
                      state:{
                        movie_id:movie.id
                      }
                    }} >
                      <img className={'recommendImg posterSmall'} src={`https://image.tmdb.org/t/p/w500${movie.poster_path }`} alt={movie.title}/>
                    </Link>
                    <h6>{movie.title}</h6>
                  </div>


            ))}
          </div>

      </div>
    );
  }
}


export default Popup;