import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {ThemeContext} from "./darkMode";

class User extends Component {
  static contextType = ThemeContext;

  deleteHistory = () =>{
    localStorage.clear();
};

  render() {
    const {darkMode, changeTheme} = this.context;
    return (
      <div className={darkMode?'darkBody':''}>
        <div className={`App container ${darkMode?'darkBodySite':''}`}>
          <nav className={`navbar sticky-top navbar-light bg-light header red margin ${darkMode?'darkHeader':''}`}>
            <Link to="/"><button className={darkMode?'btn btn-secondary':'btn btn-warning'} >
              <i className="fas fa-arrow-left"/>
            </button></Link>
            <button className={darkMode?'btn btn-secondary':'btn btn-warning'} onClick={this.deleteHistory}>
              <i className="fas fa-trash"/>
            </button>
            <button className={darkMode?'btn btn-secondary':'btn btn-warning'} onClick={()=>{changeTheme()}}>
              {darkMode? <i className="fas fa-sun"/>: <i className="fas fa-moon"/>}
            </button>
          </nav>
          <div className='d-flex flex-wrap justify-content-start'>
            {JSON.parse(localStorage.getItem('history'))
              ?
              JSON.parse(localStorage.getItem('history')).map((recommend)=>{
                return (
                  <div className={'marginAuto'} key={recommend.id}>

                    <div  className={`movieTableRecommends movieTablePopSmall marginAuto ${darkMode?'darkTableMovie colorText':''}`} >
                      <Link className={'colorText'} to={{
                        pathname:`/MoviePage/${recommend.id}`,
                        state:{
                          movie_id:recommend.id
                        }
                      }} >
                        <img className={'recommendImg posterSmall'} src={`https://image.tmdb.org/t/p/w500${recommend.poster_path }`} alt={recommend.title}/>
                      </Link>
                      <h6>{recommend.title}</h6>
                    </div>

                  </div>
                );
              })
            :
              <div>
                <h2>Your story is empty</h2>
              </div>}
          </div>
        </div>
      </div>
    );
  }
}

export default User;