import React, {useContext} from "react";
import {ThemeContext} from "./darkMode";


const Btn= (props)=>{
  const theme = useContext(ThemeContext);
    return(

           <div className={'navPos pagination marginAuto'}>
             <button onClick={props.previousPage} className={` paginationButton ${theme.darkMode?'btn btn-secondary':'btn btn-warning'}`} >
               <i className="fas fa-angle-double-left"/>
             </button>
             <button onClick={props.nextPage} className={` paginationButton ${theme.darkMode?'btn btn-secondary':'btn btn-warning'}`} >
               <i className="fas fa-angle-double-right"/>
             </button>
           </div>


    );
};

export default Btn;