import React, {Component} from 'react';
import PopMovie from "./pop";
import {ThemeContext} from "./darkMode";

class Cart extends Component {
 static contextType = ThemeContext;



  render() {
    const {darkMode} = this.context;
    const {moviesWillWatch,togglePopup}=this.props;
    return (
      <div className={'width-15'}>
        <button onClick={togglePopup} className={`cartWidth ${darkMode?'btn btn-secondary':'btn btn-warning'}`}>
          <i  className={"fas fa-heart"}/>
          {moviesWillWatch.length}
        </button>
      </div>
    );
  }
}

export default Cart;