import React, {useContext} from "react";
import {Link} from "react-router-dom"
import {ThemeContext} from "./darkMode";


const MoviesRec =props=>{
  const theme = useContext(ThemeContext);
  return(
    <div className={'movies width-80 marginAuto'}>
        { props.recommend.map((recommend)=>{
            return (
              <div className={'marginAuto'} key={recommend.id}>

                    <div  className={`movieTableRecommends marginAuto movieTablePopSmall ${theme.darkMode?'darkTableMovie colorText':''}`} onClick={props.movieId} >
                      <Link className={'colorText'} to={{
                        pathname:`/MoviePage/${recommend.id}`,
                        state:{
                          movie_id:recommend.id
                        }
                      }} >
                        <img onClick={()=>(props.addHistory(recommend))} className={'recommendImg posterSmall'} src={`https://image.tmdb.org/t/p/w500${recommend.poster_path }`} alt={recommend.title}/>
                      </Link>
                      <h6>{recommend.title}</h6>
                    </div>

              </div>
            );
        }) }
    </div>
  )
};

export default MoviesRec;