import React from "react";
import {Link} from "react-router-dom";

const MovieP = props =>{
    return(
      <div>
        <div className={'d-flex secondPage margin'}>
            <div className={'imgDiv'}>
                <img className={'poster secondPoster'} src={`https://image.tmdb.org/t/p/w500${ props.activeMovie.poster_path}`} alt={props.activeMovie.title}/>
            </div>
            <div>
                <h2>{props.activeMovie.title}</h2>
                <h4>{props.activeMovie.tagline}</h4>
                <div>{props.activeMovie.overview}</div>
                <h6>Rating: {props.activeMovie.vote_average}</h6>
                <h6>Budget: {props.activeMovie.budget}$</h6>
                <h6>Status: {props.activeMovie.status}</h6>
                <h6>Release Date: {props.activeMovie.release_date}</h6>


            </div>
        </div>
      </div>
    );
};

export default MovieP;