import React, {Component, useState} from "react";
import {Link} from "react-router-dom"
import {ThemeContext} from "./darkMode";

class Movies extends Component{
    state={
        willWatch: false
    };
  static contextType=ThemeContext;
    render() {

        const {darkMode} = this.context;
        const {results,key,addMovieToWillWatch,addMovieToHistory,removeMovieFromWillWatch}= this.props;
        return (
          <div key={key} className={`movieTable marginAuto movieTableSmall width100Small ${darkMode?'darkTableMovie':''}`}>
              <div className={'wrapper wrapperHeightSmall wrapperHeightMedium'}>
                  <img className={'width100Small'} src={`https://image.tmdb.org/t/p/w500${results.backdrop_path || results.poster_path}`}
                       alt={results.title}/>
              </div>
              <h2>{results.title}</h2>
              <div className={'textOverview textOverviewSmall'}>{results.overview}</div>
              <Link to={{
                  pathname: `/MoviePage/${results.id}`,
                  state: {
                      movie_id: results.id
                  }
              }}>
                  <button onClick={()=>(addMovieToHistory(results))} className={'btn btn-danger buttonMorePositionSmall  buttonMorePosition float'}>
                      More
                  </button>
              </Link>
              {this.state.willWatch ? (
                <button
                  type="button"
                  className="btn btn-danger buttonAddPositionSmall buttonAddPosition"
                  onClick={()=> {
                      this.setState({
                          willWatch: false
                      });
                      removeMovieFromWillWatch(results)
                  }}
                >
                    <i className="fas fa-heart"/>
                </button>
              ) : (
                <button type="button" className="btn btn-secondary buttonAddPositionSmall buttonAddPosition" onClick={()=> {
                    this.setState({
                        willWatch: true
                    });
                    addMovieToWillWatch(results)}
                }>
                    <i className="far fa-heart"/>
                </button>
              )
              }


          </div>
        )

    }



};
export default Movies;